"use client";

import Switch from "@/components/ui/switch";
import { useState } from "react";
import { z } from "zod";

const loginSchema = z.object({
	user_name: z.string(),
	password: z.string(),
});

const registerSchema = z.object({
	first_name: z.string(),
	last_name: z.string(),
	user_name: z.string(),
	password: z.string(),
});

export default function LandingPage() {
	const [darkMode, setDarkMode] = useState(window.matchMedia("(prefers-color-scheme: dark)").matches);

	const handleCheckChange = (checked: boolean) => {
		setDarkMode(checked);
	};

	console.log(darkMode);

	return (
		<div className={`flex-col bg-background min-h-[100vh] min-w-[100vw] ${darkMode ? "dark" : ""}`}>
			<div className={`flex justify-between`}>
				<div className="flex max-w-[50%] min-h-fit pl-4 pt-4 items-baseline">
					<img src="/AMPLogo.png" alt="AMP Logo" className="max-w-[15%] min-w-[15%]" />
					<span className="text-xl font-bold w-fit text-primary">Customer Service Portal</span>
				</div>
				<Switch id="darkLightMode" className="self-center mr-8" checked={darkMode} onCheckedChange={handleCheckChange} />
			</div>
		</div>
	);
}
